.modal_container {
    position: fixed;
    display: flex;
    flex-direction: column;
    text-align: center;
    background-color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 18px;
    padding: 20px;
    border-radius: 15px;
    top: 10vh;
    bottom:35vh;
    overflow-y: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
    left: 20%;
    right: 20%;
    z-index: 30;
    animation: transitionIn 1.5s;
}

.modal_container::-webkit-scrollbar {
  display: none;
}

.modal_button {
    width: 100px;
    height: 60px;
    margin-right: auto;
    margin-left: auto;
    margin-top: 2rem;
    font-size: 20px;
    background-color: white;
    border: solid;
    border-color: black;
    border-width: 3px;
    border-radius: 15px;
    cursor: pointer;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 30;
    background-color: rgba(0, 0, 0, 0.75);
}
  
  .modal_button:hover {
    background-color: black;
    color: white;
    transition: 0.5s;
  }

  @keyframes transitionIn {
    from {
        opacity: 0;
    } to {
        opacity: 1;
    }
  }